<script>
const axios = require('axios');
export default {
  name: "index-page",

  data() {
    return {
      t1:'',
      t2:'',
      t3:'',
      status1:'',
      status2:'',
      status3:'',
      fits: ['contain'],
      url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
      Locktable:[],
      tableData1:[],
      tableData2:[],
      tableData3:[]
    };
  },

  mounted() {
    this.getInfo()
  },
  methods: {
    getInfo(){
      axios.get("/index")
      .then(response => (
        console.log(response),
        console.log(response.data.status1),
        console.log(response.data.status2),
        this.Locktable = response.data.data,
        this.tableData1=response.data.data1,
        this.tableData2=response.data.data2,
        this.tableData3 =response.data.data3,
        this.status1=response.data.status1,
        this.status2=response.data.status2,
        this.status3=response.data.status3,
        console.log(this.status1),
        console.log(this.status2),
        console.log(this.status3),
        this.getStatus()
        ))
      .catch(function(error) {
          console.log(error);
      })
    },
    getStatus(){

      if(this.status1 == 2 )
      {
        this.t1='warning'
      }else if (this.status1==3){
        this.t1='danger'
        console.log(33)
      }else {
        console.log(44)
        this.t1='success'
      }

      if(this.status2 == 2 )
      {
        this.t2='warning'
      }else if (this.status2==3){
        this.t2='danger'
      }else {
        this.t2='success'
      }

      if(this.status3== 2 )
      {
        this.t3='warning'
      }else if (this.status3==3){
        this.t3='danger'
      }else {
        this.t3='success'
      }

    },
    handleFin(i){
      switch (i) {
        case 1:
            axios.get("/index/finish1")
            .catch(function (error) {
              console.log(error);
            })
          break
        case 2:
          axios
            .get("/index/finish2")
            .catch(function (error) {
              console.log(error);
            })
            break
        case 3:
          axios
            .get("/index/finish3")
            .catch(function (error) {
              console.log(error);
            })
            break    
        default:
          break
      }
      this.getInfo()
    }

  },
}


